.container{
    width: 640px;
    margin: auto;
    margin-top: 10px;
    background: white;
    color:#262626;
    display: flex;
    flex-direction:column;
    gap:20px;
    border-radius: 10px;
    padding: 40px 50px;
}
.container hr{
    height: 2px;
    border:none;
    background: #707070;
}
.container h2{
    font-size: 20px;
    font-weight: 500;
}
.container ul li
{
    display: flex;
    align-items: center;
    height: 70px;
    padding-left: 15px;
    border:1px solid #686868;
    border-radius:8px;
    margin-bottom: 20px;
    font-size: 20px;
    cursor: pointer;
}

.container button{
    margin: auto;
    width: 250px;
    height: 65px;
    background: #553f9a;
    color:#fff;
    font-size:25px;
    font-weight: 500;
    border-radius:8px;
    cursor: pointer;
}
.container .index{
  margin: auto;
  font-size: 20px;
}
.container .correct{
    background: #dffff2;
    background-color: #00d397;
}
.container .Wrong{
    background: #FFEBEB;
    border-color: #FF4A4A;
}


@media(max-width:768px){
    .container{
        width: 740px;
    }
}